h3 {
    font-size: 24px;
    color: #524743;
    text-align: center;
    display:block;
    float:left;
}

.box {
    display: block;
    width: 100% !important;
    height: auto !important;
    margin-bottom:5px;
    float:left;
}

h2 {
    font-size: 40px;
    color: #d26347;
    text-align: left;
    padding-left: 18%;
    
}

.building {
    padding-top: 140px;
    padding-bottom: 100px;
}

.gallery {
    padding: 50px;
    line-height: 0;
    -webkit-column-count:3;
    -webkit-column-gap:8px;
    -moz-column-count:3;
    -moz-column-gap:8px;
    column-count:3;
    column-gap:5px;
}

.gallery img {
    width: 100% !important;
    height: auto !important;
}

@media (max-width:1400px) {
    .gallery {
        -moz-column-count:2;
        -webkit-column-count:2;
        column-count:2;
    }
}

@media (max-width:1000px) {
    .gallery {
        -moz-column-count:1;
        -webkit-column-count:1;
        column-count:1;
    }
}